<template>
  <div class="main">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item prop="num" label="工单编号:">
        <el-input
          clearable
          v-model="formInline.num"
          placeholder="请输入"
        ></el-input>
      </el-form-item>
      <el-form-item prop="region" label="工单状态:">
        <el-select clearable v-model="formInline.region" placeholder="请选择  ">
          <el-option
            v-for="item in Selectfrom"
            :key="item.statusId"
            :label="item.statusName"
            :value="item.statusId"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <div class="textmain">
      <div class="textmainTop">
        <el-button
          class="mediumone"
          size="medium "
          icon="el-icon-circle-plus-outline"
          type="warning"
          @click.native="dialogAddformFn"
          >新建</el-button
        >
        <el-button
          class="mediumtwo"
          plain
          size="medium "
          type="warning"
          @click="attachmentFN"
          >工单配置</el-button
        >
      </div>
      <el-dialog
        title="补货详情"
        class="ChannelList"
        :visible.sync="Capturedialog"
        width="40%"
      >
        <el-table :data="Capturelist">
          <el-table-column
            property="channelCode"
            label="货道编号"
            width="120"
          ></el-table-column>
          <el-table-column
            property="sku.brandName"
            label="物品名称"
            width="120"
          ></el-table-column>
          <el-table-column
            property="currentCapacity"
            label="当前数量"
            width="120"
          >
          </el-table-column>
          <el-table-column
            label="还可添加"
            property="maxCapacity"
            width="120"
          ></el-table-column>
          <el-table-column label="补满数量" property="maxCapacity">
            <el-input-number
              v-model="addnum"
              controls-position="right"
              @change="handleChange"
              :min="0"
              :max="10"
            ></el-input-number>
          </el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer" style="text-align: center">
          <el-button @click="Capturedialog = false">取 消</el-button>
          <el-button type="primary" @click="Capturedialog = false"
            >确 定</el-button
          >
        </div>
      </el-dialog>
      <el-dialog
        title="新增工单"
        :visible.sync="dialogAddform"
        width="32%"
        @close="dialogAddformCancel"
      >
        <el-form
          :model="Addform"
          label-width="100px"
          style="padding-left: 50px"
          :rules="Addformrules"
          ref="Addform"
        >
          <el-form-item prop="numb" label="设备编号：">
            <el-input
              @input="Inputtask"
              placeholder="请输入"
              style="width: 90%"
              v-model="Addform.numb"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item prop="value1" label="工单类型：">
            <el-select
              style="width: 90%"
              v-model="Addform.value1"
              clearable
              placeholder="请选择"
            >
              <el-option
                v-for="item in selectList"
                :key="item.typeId"
                :label="item.typeName"
                :value="item.typeId"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <span style="margin-left: -82px"> 补货数量：</span
            ><span
              @click="CapturelistFn"
              style="margin-left: 20px; color: #5f84ff; cursor: pointer"
              ><i class="el-icon-document" style="font-size: 19px"></i>
              捕获清单</span
            >
          </el-form-item>
          <el-form-item prop="value2" label="运营人员：">
            <el-select
              style="width: 90%"
              v-model="Addform.value2"
              clearable
              placeholder="请选择"
            >
              <el-option
                v-for="item in Inputtasklist"
                :key="item.userId"
                :label="item.userName"
                :value="item.userId"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="remark" label="备注：">
            <el-input
              type="textarea"
              placeholder="请输入备注(40字之内)"
              style="width: 90%"
              v-model="Addform.remark"
              autocomplete="off"
              maxlength="40"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer" style="text-align: center">
          <el-button @click="dialogAddformCancel">取 消</el-button>
          <el-button type="primary" @click="dialogAddformTrue">确 定</el-button>
        </div>
      </el-dialog>
      <el-dialog
        title="工单配置"
        :visible.sync="dialogwarnform"
        width="30%"
        @close="dialogwarnform = false"
      >
        <el-form :model="warnform" ref="warnform">
          <el-form-item
            type="number"
            prop="numb"
            :rules="[
              { required: true, message: '数据不能为空' },
              {
                type: 'number',
                message: '数据必须为数字',
              },
            ]"
            label="补货警戒线："
          >
            <el-input
              oninput="if(value>30)value=100"
              style="width: 80%"
              v-model.number="warnform.numb"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer" style="text-align: center">
          <el-button @click="dialogwarnform = false">取 消</el-button>
          <el-button type="primary" @click="warnformEntrue">确定</el-button>
        </div>
      </el-dialog>
      <!-- 查看详情 -->
      <el-dialog
        title="工单详情"
        :visible.sync="Viewdetails"
        width="30%"
        :before-close="ViewdetailsFn"
        class="Viewdetails"
      >
        <template>
          <div class="top" style="height: 54px">
            <h2>
              <i :class="Viewdetailsicon"></i>
              <span style="font-size: 14px">{{ Viewdetailsspan }}</span
              ><img
                style="float: right; margin-top: -30px; margin-right: 65px"
                :src="Viewdetailsimg"
                alt=""
              />
            </h2>
          </div>
          <el-row>
            <el-col :span="12"
            style="height:30px"
              ><div class="grid-content bg-purple">
                设备编号：{{ ViewdetailsList.innerCode }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple-light">
                创建日期：{{ ViewdetailscreateTime }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple">
                取消日期：{{ ViewdetailsupdateTime }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple-light">
                运维人员：{{ ViewdetailsList.userName }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple">
                工单类型：{{ ViewdetailstypeName }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple-light">
                补货数量：<span
                  @click="CapturelistFn"
                  style="margin-left: 20px; color: #5f84ff; cursor: pointer"
                  ><i class="el-icon-document" style="font-size: 19px"></i>
                  捕获清单</span
                >
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple-light">
                工单方式：{{ Viewdetailstype }}
              </div></el-col
            >
            <el-col :span="12"
             style="height:30px"
              ><div class="grid-content bg-purple">取消原因：</div></el-col
            >
          </el-row>
        </template>
        <div slot="footer" style="text-align: center" v-if="ViewdetailsShow">
          <span class="dialog-footer">
            <el-button type="primary" @click="Viewdetailsfrom()">{{
              ViewdetailsName === "待办" ? "取消订单" : "重新创办"
            }}</el-button>
          </span>
        </div>
      </el-dialog>
      <el-table :data="tableData.currentPageRecords" style="width: 100%">
        <el-table-column prop="pageid" label="序号" width="80">
        </el-table-column>
        <el-table-column prop="taskCode" label="工单编号" width="200">
        </el-table-column>
        <el-table-column prop="innerCode" label="设备编号" width="200">
        </el-table-column>
        <el-table-column prop="taskType.typeName" label="工单类型" width="200">
        </el-table-column>
        <el-table-column prop="createType" label="工单方式" width="200">
        </el-table-column>
        <el-table-column
          prop="taskStatusTypeEntity.statusName"
          label="工单状态"
          width="200"
        >
        </el-table-column>
        <el-table-column prop="userName" label="运营人员" width="200">
        </el-table-column>
        <el-table-column prop="createTime" label="创建日期" width="200">
        </el-table-column>
        <el-table-column prop="taskId" label="操作" width="100">
          <template v-slot="{ row }">
            <span @click="ViewdetailsfirstFn(row.taskId)">查看详情</span>
          </template>
        </el-table-column>
      </el-table>

      <div class="textmainfoot" style="margin-top: 20px">
        <span
          >共{{ tableData.totalCount }}条记录 第 {{ tableData.pageIndex }}/{{
            tableData.totalPage
          }}
          页</span
        >
        <div class="textmainfoot1">
          <el-button :disabled="Next1" @click="pagenumFn(-1)">上一页</el-button>
          <el-button :disabled="Next2" @click="pagenumFn(1)">下一页</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getworkstatus,
  getworktask,
  supplyAlertValue,
  supplyConfigValue,
  supplySelectList,
  AddtaskService,
  Inputtask,
  ChannelList,
  TaskInfo,
  Taskcancel
} from '@/api/task'
export default {
  data () {
    return {
      formInline: {
        num: '',
        region: ''
      },
      Selectfrom: [],
      tableData: '',
      pagenum: 1,
      Next1: true,
      Next2: false,
      dialogwarnform: false,
      dialogAddform: false,
      Addform: {
        numb: '',
        value1: '',
        value2: '',
        remark: ''
      },
      Addform2: {
        numb: '',
        value1: '',
        value2: 23,
        remark: ''
      },
      selectList: [],
      Addformrules: {
        numb: [
          {
            required: true,
            message: '请输入编码',
            trigger: ['blur', 'change']
          }
        ],
        value1: [
          {
            required: true,
            message: '请选择工单类型',
            trigger: ['blur', 'change']
          }
        ],
        value2: [
          {
            required: true,
            message: '请选择运营人员',
            trigger: ['blur', 'change']
          }
        ],
        remark: [
          {
            required: true,
            message: '请输入备注',
            trigger: ['blur', 'change']
          }
        ]
      },
      ArtifactList: [],
      Inputtasklist: [],
      warnform: {
        numb: ''
      },
      Capturedialog: false,
      Capturelist: [],
      Capturelist2: [],
      num: 0,
      addnum: 10,
      // 查看详情数据
      Viewdetails: false,
      ViewdetailsShow: true,
      ViewdetailsList: {},
      ViewdetailscreateTime: '',
      ViewdetailsupdateTime: '',
      ViewdetailstypeName: '',
      Viewdetailstype: 0,
      Viewdetailsspan: '',
      Viewdetailsimg: '',
      Viewdetailsicon: '',
      ViewdetailsName: '',
      ID: ''
    }
  },

  methods: {
    async TaskInfoFn (id) {
      this.ViewdetailsList = await TaskInfo(id)
      this.Addform2 = {
        numb: this.ViewdetailsList.innerCode,
        value1: this.ViewdetailstypeName,
        value2: this.ViewdetailsList.assignorId,
        remark: this.ViewdetailsList.desc
      }

      this.ViewdetailstypeName = this.ViewdetailsList.taskType.typeName
      this.ViewdetailstaskType = this.ViewdetailsList.taskType.Type
      this.ViewdetailsName =
        this.ViewdetailsList.taskStatusTypeEntity.statusName
      if (this.Viewdetailstype === 1) {
        this.Viewdetailstype = '手动'
      }
      if (this.Viewdetailstype === 0) {
        this.Viewdetailstype = '自动'
      }
      this.ViewdetailscreateTime = this.ViewdetailsList.createTime.replace(
        'T',
        'ㅤ'
      )
      this.ViewdetailsupdateTime = this.ViewdetailsList.updateTime.replace(
        'T',
        'ㅤ'
      )
      if (this.ViewdetailsList.taskStatusTypeEntity.statusName === '完成') {
        this.Viewdetailsspan = '完成'
        this.Viewdetailsicon = 'el-icon-success'
        this.Viewdetailsimg =
          'https://likede2-admin.itheima.net/img/pic_4.3b5af41c.png'
        this.ViewdetailsShow = false
      }
      if (this.ViewdetailsList.taskStatusTypeEntity.statusName === '取消') {
        this.Viewdetailsspan = '取消'
        this.Viewdetailsicon = 'el-icon-warning'
        this.Viewdetailsimg =
          'https://likede2-admin.itheima.net/img/pic_3.e8208e34.png'
      }
      if (this.ViewdetailsList.taskStatusTypeEntity.statusName === '待办') {
        this.Viewdetailsspan = '待办'
        this.Viewdetailsicon = 'el-icon-alarm-clock'
        this.Viewdetailsimg =
          'https://likede2-admin.itheima.net/img/pic_1.834b274c.png'
      }
      if (this.ViewdetailsList.taskStatusTypeEntity.statusName === '进行') {
        this.Viewdetailsspan = '进行'
        this.Viewdetailsicon = 'el-icon-more-outline'
        this.Viewdetailsimg =
          'https://likede2-admin.itheima.net/img/pic_2.3b5af41c.png'
      }
    },
    async TaskcancelFn (id) {
      await Taskcancel(id)
    },
    ViewdetailsfirstFn (id) {
      this.TaskInfoFn(id)
      this.id = id
      this.Viewdetails = true
    },
    Viewdetailsfrom () {
      if (this.ViewdetailsName === '待办') {
        this.$confirm('取消工单后，将不能恢复，是否确认取消？', '确认信息', {
          distinguishCancelAndClose: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        })
          .then(() => {
            const res = this.TaskcancelFn(this.id)
            console.log(res)
            this.$message({
              type: 'success',
              message: '取消成功'
            })
            this.Viewdetails = false
            this.onSubmit(1)
          })
          .catch((action) => {
            this.$message({
              type: 'info',
              message: '放弃保存'
            })
          })
        return
      }
      this.Viewdetails = false
      this.dialogAddform = true
      this.Addform = this.Addform2
      this.Inputtask()
    },
    ViewdetailsFn (done) {
      this.$confirm('确认关闭？')
        .then((res) => {
          done()
          this.ViewdetailsShow = true
        })
        .catch((e) => {
          console.log(e)
        })
    },
    async CapturelistFn () {
      if (this.Addform.numb !== '') {
        this.Capturedialog = true
        await this.ChannelListFn(1)
        // Capturelist2.push({
        // })
        // channelCode: '1-1'
        // expectCapacity: 10
        // skuId: '2'
        // skuImage: 'http://lkd2-java.itheima.net/image/product2.png'
        // skuName: '康师傅冰红茶'
      }
      if (this.Addform2.numb !== '') {
        this.Capturedialog = true
        await this.ChannelListFn(2)
      }
    },
    dialogAddformFn () {
      this.dialogAddform = true
      this.Inputtask()
    },
    dialogAddformCancel () {
      this.Addform = { numb: '', value1: '', value2: '', remark: '' }
      this.$refs.Addform.resetFields()
      this.Inputtasklist = []
      this.dialogAddform = false
    },
    dialogAddformTrue () {
      this.$refs.Addform.validate(async (valid) => {
        if (valid) {
          await this.AddtaskServiceFn()
            .then((res) => {
              this.$message.success('添加成功')
              this.dialogAddform = false
            })
            .catch((e) => {
              this.$message.error('添加失败了，' + e.response.data)
            })
        } else {
          this.$message.error('请填好表单')
          return false
        }
      })
    },
    onSubmit (num) {
      if (num !== 1) {
        this.pagenum = 1
      }
      this.getworktaskFn({
        taskCode: this.formInline.num,
        isRepair: false,
        status: this.formInline.region,
        pageIndex: this.pagenum
      })
    },
    async getworktaskFn (data) {
      const res = await getworktask({
        innerCode: this.formInline.num,

        ...data
      })
      res.currentPageRecords.forEach((item, index) => {
        item.pageid = index + 1 + (res.pageIndex - 1) * res.pageSize
        if (item.createType === 1) {
          item.createType = '手动'
        }
        if (item.createType === 0) {
          item.createType = '自动'
        }
        item.createTime = item.createTime.replace('T', 'ㅤ')
      })
      this.tableData = res
    },
    pagenumFn (num) {
      if (num === -1) {
        if (this.pagenum + num === 1) {
          this.Next1 = true
        } else {
          this.Next1 = false
        }
      }
      if (this.pagenum === Number(this.tableData.totalPage - 1)) {
        this.Next2 = true
      } else {
        this.Next2 = false
      }
      if (
        this.pagenum + num === 0 ||
        this.pagenum === this.tableData.totalPage
      ) {
        return
      }
      this.pagenum += num
      this.onSubmit(1)
    },
    async getworkstatusFn () {
      this.Selectfrom = await getworkstatus()
    },
    async supplyAlertValueFn () {
      this.warnform.numb = await supplyAlertValue()
    },
    async supplyConfigValueFn (data) {
      const res = await supplyConfigValue(data)
      console.log(res)
    },
    warnformEntrue () {
      this.$refs.warnform.validate((valid) => {
        if (valid) {
          this.$message.success('修改成功')
          this.supplyConfigValueFn(this.warnform.numb)
          this.dialogwarnform = false
        } else {
          this.$message.error('修改失败')
          return false
        }
      })
    },
    attachmentFN () {
      this.dialogwarnform = true
      this.supplyAlertValueFn()
    },
    async supplySelectListFn () {
      // this.selectList = supplySelectList().filter((item) => (item.type = 1))
      const res = await supplySelectList()
      this.selectList = res.filter((item) => item.type === 2)
    },
    async AddtaskServiceFn () {
      await AddtaskService({
        createType: 1,
        innerCode: this.Addform.numb,
        assignorId: this.Addform.value2,
        productType: this.Addform.value1,
        desc: this.Addform.remark,
        details: this.Capturelist2,
        userId: 1
      })
      this.Capturelist2 = []
    },
    async Inputtask () {
      this.Inputtasklist = await Inputtask(this.Addform.numb)
    },
    async ChannelListFn (num) {
      if (num === 1) {
        this.Capturelist = await ChannelList(this.Addform.numb)
        this.Capturelist.forEach((item) => {
          this.Capturelist2.push({
            channelCode: item.channelCode,
            expectCapacity: item.maxCapacity,
            skuId: item.skuId,
            skuImage: item.sku.skuImage,
            skuName: item.sku.skuName
          })
        })
      } else {
        this.Capturelist = await ChannelList(this.Addform2.numb)
        // this.Addform2.numb = ''
      }
    },
    handleChange (value) {
      console.log(value)
    }
  },

  created () {
    TaskInfo()
    this.supplySelectListFn()
    this.supplyAlertValueFn()
    this.getworkstatusFn()
    this.onSubmit()
  }
}
</script>

