    }
    .el-table .el-table__header-wrapper {
      .has-gutter th {
        height: 100%;
        background-color: #999 !important;
      }
    }
    .textmainfoot {
      height: 96px;
      line-height: 96px;
      span {
        -webkit-box-flex: 1;
